<template>
  <div id="action-bar">
    <Tabs :value="value" :tabs="tabs" @input="handleInput" />

    <q-space />

    <BaseActionButton
      icon="eva-question-mark"
      color="secondary"
      @click="$emit('quickHelp')"
    />

    <BaseButton
      icon="eva-save-outline"
      label="save"
      class="q-ml-md"
      :is-loading="isLoading"
      @click="$emit('save')"
    />
  </div>
</template>

<script>
import Tabs from "@/components/common/tabs/Tabs.vue";

export default {
  name: "ActionBar",

  components: { Tabs },

  props: {
    value: {
      type: String,
      required: true,
    },

    isLoading: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      tabs: [
        {
          id: this.$nano.id(),
          label: "workspace details",
          value: "WORKSPACE_DETAILS",
        },
      ],
    };
  },

  methods: {
    handleInput(tab) {
      this.$emit("input", tab);
    },
  },
};
</script>

